	module.exports = {"pathContext":{"docHtml":"<h1>Resource Health</h1>\n<h2>Overview</h2>\n<p>ArgoCD provides built-in health assessment for several standard Kubernetes types, which is then\nsurfaced to the overall Application health status as a whole. The following checks are made for\nspecific types of kuberentes resources:</p>\n<h3>Deployment, ReplicaSet, StatefulSet DaemonSet</h3>\n<ul>\n<li>Observed generation is equal to desired generation.</li>\n<li>Number of <strong>updated</strong> replicas equals the number of desired replicas.</li>\n</ul>\n<h3>Service</h3>\n<ul>\n<li>If service type is of type <code>LoadBalancer</code>, the <code>status.loadBalancer.ingress</code> list is non-empty,\nwith at least one value for <code>hostname</code> or <code>IP</code>.</li>\n</ul>\n<h3>Ingress</h3>\n<ul>\n<li>The <code>status.loadBalancer.ingress</code> list is non-empty, with at least one value for <code>hostname</code> or <code>IP</code>.</li>\n</ul>","docPath":"argo-cd/docs/health","proj":"argo-cd"}}

